      ],
      selectedSub: {
        sid: 0,
        name: "",
        ssid: 0,
        numbers: 0,
        time: new Date().value,
        copied: false,
        similar: 0,
        grade: 0
      }
    };
  },
  methods: {
    check() {
      let _this = this;
      for (let it of this.submissions) {
        if (it.grade === 0) {
          _this.exportDialog = true;
          break;
        }
      }
      this.$getRequest("/apis/teacher/grade");
    },
    editItem(item) {
      this.selectedSub = item;
      this.editDialog = true;
    },
    update() {
      let _this = this;
      let param = {
        grade: _this.selectedSub.grade
      };
      this.$putRequest(
        "/apis/teacher/submissions/" + this.selectedSub.sid,
        param
      ).then(res => {
        if (res.data === true) {
          _this.editDialog = false;
        }
      });
    }
  },
  mounted() {
    let _this = this;
    this.$getRequest("/apis/teacher/submissions/of/" + this.hid)
      .then(res => {
        _this.submissions = res.data;
      })
      .catch(err => {
        console.log(err);
      });
  }
};
